"use client";

import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import { useAuth } from "@/contexts/AuthContext";
import { createClient } from "@/utils/supabase/client";
import { Button } from "@/components/ui/button";
import {
  Card,
  CardContent,
  CardDescription,
  CardFooter,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { CheckCircle2, CreditCard, Loader2 } from "lucide-react";
import { motion } from "framer-motion";

export default function PaymentPage() {
  const router = useRouter();
  const { user } = useAuth();
  const [isCreatingCheckout, setIsCreatingCheckout] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [paymentCompleted, setPaymentCompleted] = useState(false);
  const [isCheckingPayment, setIsCheckingPayment] = useState(true);
  const [sessionReady, setSessionReady] = useState(false);
  const [debugInfo, setDebugInfo] = useState<string[]>([]);

  const addDebugLog = (message: string) => {
    const timestamp = new Date().toISOString();
    const logMessage = `${timestamp}: ${message}`;
    console.log(logMessage);
    setDebugInfo((prev) => [...prev, logMessage].slice(-10)); // Keep last 10 logs
  };

  useEffect(() => {
    const initializePaymentPage = async () => {
      addDebugLog("=== PAYMENT PAGE INIT ===");
      const supabase = createClient();

      // Check auth context first
      addDebugLog(`Auth context user: ${user ? user.id : "null"}`);
      addDebugLog(`Auth context email: ${user ? user.email : "null"}`);

      // Try to refresh/sync session first
      addDebugLog("Attempting to refresh session...");
      const { data: refreshData, error: refreshError } =
        await supabase.auth.refreshSession();

      if (refreshData.session) {
        addDebugLog("Session refresh successful");
        addDebugLog(`Refreshed user: ${refreshData.session.user.id}`);
      } else {
        addDebugLog(
          `Session refresh failed: ${refreshError?.message || "unknown"}`
        );
      }

      // Now check for session
      const {
        data: { session },
        error: sessionError,
      } = await supabase.auth.getSession();

      addDebugLog(`Session check result:`);
      addDebugLog(`- hasSession: ${!!session}`);
      addDebugLog(`- userId: ${session?.user?.id || "null"}`);
      addDebugLog(`- userEmail: ${session?.user?.email || "null"}`);
      addDebugLog(
        `- emailConfirmed: ${session?.user?.email_confirmed_at || "null"}`
      );
      addDebugLog(`- sessionError: ${sessionError?.message || "none"}`);
      addDebugLog(
        `- accessToken: ${session?.access_token ? "present" : "missing"}`
      );
      addDebugLog(
        `- refreshToken: ${session?.refresh_token ? "present" : "missing"}`
      );
      addDebugLog(`- expiresAt: ${session?.expires_at || "null"}`);

      // Try to get user directly
      const { data: userData, error: userError } =
        await supabase.auth.getUser();
      addDebugLog(`Direct user check:`);
      addDebugLog(`- hasUser: ${!!userData.user}`);
      addDebugLog(`- userId: ${userData.user?.id || "null"}`);
      addDebugLog(`- userError: ${userError?.message || "none"}`);

      // If still no session but we expected one (coming from signup), try manual sync
      if (!session && !user && !userData.user) {
        addDebugLog(
          "No session found anywhere, checking URL params for signup context..."
        );

        // Check if we came from signup (could add a URL parameter for this)
        const urlParams = new URLSearchParams(window.location.search);
        const fromSignup = urlParams.get("from") === "signup";

        if (fromSignup) {
          addDebugLog(
            "Came from signup, attempting additional session recovery..."
          );

          // Try one more time with a slight delay
          await new Promise((resolve) => setTimeout(resolve, 500));

          const { data: retrySession, error: retryError } =
            await supabase.auth.getSession();
          addDebugLog(
            `Retry session check: ${!!retrySession.session}, error: ${
              retryError?.message || "none"
            }`
          );

          if (retrySession.session) {
            addDebugLog("Session recovered on retry!");
            setSessionReady(true);
            await checkPaymentStatus(retrySession.session.user.id);
            return;
          }
        }

        addDebugLog("No session found anywhere, redirecting to signup");
        router.push("/sign-up");
        return;
      }

      // Check if session/user mismatch
      if (session && userData.user && session.user.id !== userData.user.id) {
        addDebugLog(`WARNING: Session user ID mismatch!`);
        addDebugLog(`- Session user: ${session.user.id}`);
        addDebugLog(`- Direct user: ${userData.user.id}`);
      }

      // Check cookies
      const cookies = document.cookie;
      const authCookies = cookies
        .split(";")
        .filter(
          (cookie) =>
            cookie.trim().startsWith("sb-") ||
            cookie.trim().includes("auth") ||
            cookie.trim().includes("session")
        );
      addDebugLog(
        `Auth-related cookies: ${
          authCookies.length > 0 ? authCookies.join(", ") : "none"
        }`
      );

      // Determine which user ID to use
      const finalUserId = user?.id || session?.user?.id || userData.user?.id;
      addDebugLog(`Final user ID to use: ${finalUserId}`);

      setSessionReady(true);

      // Check payment status
      if (finalUserId) {
        await checkPaymentStatus(finalUserId);
      } else {
        addDebugLog("ERROR: No user ID available for payment check");
      }
    };

    initializePaymentPage();

    // Listen for auth state changes
    const supabase = createClient();
    const {
      data: { subscription },
    } = supabase.auth.onAuthStateChange((event, session) => {
      addDebugLog(`Auth state changed: ${event}`);
      addDebugLog(`- New session: ${!!session}`);
      addDebugLog(`- New user: ${session?.user?.id || "null"}`);

      if (event === "SIGNED_IN" && session) {
        addDebugLog(
          "User signed in via auth state change, checking payment status"
        );
        checkPaymentStatus(session.user.id);
      } else if (event === "SIGNED_OUT") {
        addDebugLog("User signed out, redirecting to signup");
        router.push("/sign-up");
      }
    });

    return () => {
      addDebugLog("Cleaning up auth state listener");
      subscription.unsubscribe();
    };
  }, [user, router]);

  const checkPaymentStatus = async (userId?: string) => {
    if (!userId) {
      addDebugLog("No user ID available for payment check");
      setIsCheckingPayment(false);
      return;
    }

    try {
      setIsCheckingPayment(true);
      addDebugLog(`Checking payment status for user: ${userId}`);

      const response = await fetch(
        `/api/onboarding/payment-status?userId=${userId}`
      );

      addDebugLog(
        `Payment status API response: ${response.status} ${response.statusText}`
      );

      if (!response.ok) {
        const errorText = await response.text();
        addDebugLog(`Payment status API error: ${errorText}`);
        throw new Error("Failed to check payment status");
      }

      const data = await response.json();
      addDebugLog(`Payment status result: ${JSON.stringify(data)}`);

      if (data.paymentCompleted) {
        addDebugLog(
          "Payment completed! Redirecting to onboarding in 2 seconds"
        );
        setPaymentCompleted(true);
        // Redirect to onboarding after a delay
        setTimeout(() => {
          addDebugLog("Executing redirect to onboarding");
          router.push("/onboarding");
        }, 2000);
      } else {
        addDebugLog("Payment not completed, staying on payment page");
      }
    } catch (error) {
      addDebugLog(`Error checking payment status: ${error}`);
      console.error("Error checking payment status:", error);
      setError("Failed to check payment status. Please refresh the page.");
    } finally {
      setIsCheckingPayment(false);
      addDebugLog("Payment status check completed");
    }
  };

  const handleCreateCheckout = async () => {
    const userId = user?.id;

    addDebugLog(`=== CREATE CHECKOUT START ===`);
    addDebugLog(`User ID from context: ${userId}`);

    if (!userId) {
      addDebugLog("ERROR: No user ID available for checkout");
      setError("You must be logged in to start a subscription");
      return;
    }

    try {
      setIsCreatingCheckout(true);
      setError(null);

      addDebugLog("Making request to create checkout session");
      const response = await fetch("/api/payment/create-checkout", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          userId: userId,
        }),
      });

      addDebugLog(
        `Checkout API response: ${response.status} ${response.statusText}`
      );

      if (!response.ok) {
        const errorData = await response.json();
        addDebugLog(`Checkout API error: ${JSON.stringify(errorData)}`);
        throw new Error(errorData.error || "Failed to create checkout session");
      }

      const data = await response.json();
      addDebugLog(`Checkout session created: ${JSON.stringify(data)}`);

      // Redirect to Stripe Checkout
      addDebugLog(`Redirecting to Stripe: ${data.url}`);
      window.location.href = data.url;
    } catch (error) {
      addDebugLog(`Checkout error: ${error}`);
      console.error("Error creating checkout session:", error);
      setError(
        error instanceof Error
          ? error.message
          : "Failed to create checkout session. Please try again."
      );
    } finally {
      setIsCreatingCheckout(false);
      addDebugLog("Checkout process finished");
    }
  };

  // Show loading while checking session
  if (!sessionReady || isCheckingPayment) {
    return (
      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        className="min-h-screen flex items-center justify-center bg-black relative overflow-hidden"
      >
        <Card className="w-full max-w-md bg-card/80 backdrop-blur-xl border border-border shadow-2xl">
          <CardContent className="p-8 text-center">
            <div className="flex flex-col items-center space-y-4">
              <div className="h-24 w-24 rounded-full bg-accent flex items-center justify-center">
                <Loader2 className="h-12 w-12 text-primary animate-spin" />
              </div>
              <div>
                <h2 className="text-2xl font-bold text-foreground">
                  {!sessionReady ? "Loading..." : "Checking Payment Status..."}
                </h2>
                <p className="text-muted-foreground mt-2">
                  Please wait while we verify your account.
                </p>
              </div>
            </div>
          </CardContent>
        </Card>
      </motion.div>
    );
  }

  if (paymentCompleted) {
    return (
      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        className="min-h-screen flex items-center justify-center bg-black relative overflow-hidden"
      >
        <Card className="w-full max-w-md bg-card/80 backdrop-blur-xl border border-border shadow-2xl">
          <CardContent className="p-8 text-center">
            <div className="flex flex-col items-center space-y-4">
              <div className="h-24 w-24 rounded-full bg-green-100 flex items-center justify-center">
                <CheckCircle2 className="h-12 w-12 text-green-600" />
              </div>
              <div>
                <h2 className="text-2xl font-bold text-foreground">
                  Payment Successful!
                </h2>
                <p className="text-muted-foreground mt-2">
                  Redirecting you to complete your profile setup...
                </p>
              </div>
            </div>
          </CardContent>
        </Card>
      </motion.div>
    );
  }

  return (
    <motion.div
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      className="min-h-screen flex items-center justify-center bg-black relative overflow-hidden"
    >
      <Card className="w-full max-w-md bg-card/80 backdrop-blur-xl border border-border shadow-2xl">
        <CardHeader className="text-center">
          <CardTitle className="text-2xl text-foreground">
            Start Your Free Trial
          </CardTitle>
          <CardDescription className="text-muted-foreground">
            Get started with our 14-day free trial
          </CardDescription>
        </CardHeader>

        <CardContent className="space-y-6">
          <div className="flex flex-col items-center justify-center p-6 space-y-4">
            <div className="h-24 w-24 rounded-full bg-accent flex items-center justify-center">
              <CreditCard className="h-12 w-12 text-primary" />
            </div>
            <div className="text-center space-y-2">
              <p className="text-lg font-medium text-foreground">
                Complete Your Registration
              </p>
              <p className="text-sm text-muted-foreground">
                Add your payment details to start your free trial. You won't be
                charged until the trial ends.
              </p>
              <div className="mt-4 space-y-2">
                {[
                  "14 days of full access",
                  "No charges during trial",
                  "Cancel anytime",
                ].map((text) => (
                  <div
                    key={text}
                    className="flex items-center justify-center space-x-2 text-sm text-muted-foreground"
                  >
                    <CheckCircle2 className="h-4 w-4 text-primary" />
                    <span className="text-foreground">{text}</span>
                  </div>
                ))}
              </div>
            </div>
          </div>

          {error && (
            <div className="bg-destructive/10 border border-destructive/20 text-destructive text-sm p-3 rounded-md">
              {error}
            </div>
          )}

          {/* Debug Information Panel */}
          <div className="bg-gray-900/50 border border-gray-700 p-3 rounded-md">
            <h3 className="text-xs font-medium text-gray-400 mb-2">
              Debug Information:
            </h3>
            <div className="max-h-32 overflow-y-auto space-y-1">
              {debugInfo.slice(-5).map((log, index) => (
                <p
                  key={index}
                  className="text-xs text-gray-500 font-mono break-all"
                >
                  {log}
                </p>
              ))}
            </div>
            <button
              onClick={() => setDebugInfo([])}
              className="text-xs text-gray-400 hover:text-gray-300 mt-2"
            >
              Clear logs
            </button>
          </div>
        </CardContent>

        <CardFooter className="flex flex-col space-y-3">
          <Button
            onClick={handleCreateCheckout}
            disabled={isCreatingCheckout}
            className="w-full bg-primary text-primary-foreground hover:bg-primary/90"
          >
            {isCreatingCheckout ? (
              <>
                <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                Creating checkout...
              </>
            ) : (
              "Start Free Trial"
            )}
          </Button>
          <Button
            variant="outline"
            onClick={() => {
              const userId = user?.id;
              addDebugLog(`Manual payment check requested for user: ${userId}`);
              if (userId) {
                checkPaymentStatus(userId);
              } else {
                addDebugLog("ERROR: No user ID for manual payment check");
              }
            }}
            disabled={isCheckingPayment || isCreatingCheckout}
            className="w-full border-border hover:bg-accent/50"
          >
            Check Payment Status
          </Button>

          <Button
            variant="outline"
            onClick={async () => {
              addDebugLog("=== MANUAL SESSION REFRESH ===");
              const supabase = createClient();
              const { data, error } = await supabase.auth.refreshSession();
              addDebugLog(
                `Session refresh result: ${!!data.session}, error: ${
                  error?.message || "none"
                }`
              );

              if (data.session) {
                addDebugLog(
                  "Session refreshed successfully, checking payment status"
                );
                checkPaymentStatus(data.session.user.id);
              }
            }}
            disabled={isCheckingPayment || isCreatingCheckout}
            className="w-full border-border hover:bg-accent/50"
          >
            Refresh Session
          </Button>
        </CardFooter>
      </Card>
    </motion.div>
  );
}
